


















































































































import { Component, Inject, Vue } from "vue-property-decorator";
import AdminTable from "@/components/admin/AdminTable.vue";
import { SELL_SERVER_URL_SYMBOL } from "@/constants";
import * as _ from "lodash";
import SkinsplusItem from "@/components/buy/SkinsplusItem.vue";
import CircleLoading from "@/components/loading/CircleLoading.vue";

@Component({
  components: {
    AdminTable,
    CircleLoading,
    SkinsplusItem
  }
})
export default class AdminUsers extends Vue {
  @Inject(SELL_SERVER_URL_SYMBOL) sellServerUrl!: string;
  showPartnerInventory = false;
  selected: any[] = [];
  partnerOnly = false;

  columns = [
    {
      name: "_id",
      required: true,
      label: "Steam ID",
      field: "_id",
      align: "left",
      type: "string"
    },
    {
      name: "providerId",
      required: true,
      label: "Steam ID",
      field: "providerId",
      align: "left",
      type: "string"
    },
    {
      name: "name",
      align: "center",
      label: "name",
      field: "name",
      editable: true,
      type: "string"
    },
    {
      name: "partner",
      align: "center",
      label: "partner",
      field: "partner",
      editable: true,
      type: "boolean"
    },
    {
      name: "partnerResultUrl",
      align: "center",
      label: "partnerResultUrl",
      field: "partnerResultUrl",
      editable: true,
      type: "string"
    },
    {
      name: "partnerRedirectUrl",
      align: "center",
      label: "partnerRedirectUrl",
      field: "partnerRedirectUrl",
      editable: true,
      type: "string"
    },
    {
      name: "partnerMinDeposit",
      align: "center",
      label: "partnerMinDeposit",
      field: "partnerMinDeposit",
      editable: true,
      type: "number"
    },
    {
      name: "partnerSteamItemCommission",
      align: "center",
      label: "partnerSteamItemCommission",
      field: "partnerSteamItemCommission",
      editable: true,
      type: "number"
    },
    {
      name: "partnerWithdrawSteamItemCommission",
      align: "center",
      label: "partnerWithdrawSteamItemCommission",
      field: "partnerWithdrawSteamItemCommission",
      editable: true,
      type: "number"
    },
    {
      name: "partnerApiKey",
      align: "center",
      label: "partnerApiKey",
      field: "partnerApiKey",
      editable: true,
      type: "string"
    },
    {
      name: "balance",
      align: "center",
      label: "balance",
      field: "balance",
      editable: true,
      type: "number"
    }
  ];
  getPartnerSteamBotItems: any = [];
  filterGetPartnerSteamBotItems: any = "";

  sumBy(args, key) {
    return _.sumBy(args, key);
  }

  onGetPartnerSteamBotItemsResult({ data }) {
    if (data && data.getPartnerSteamBotItems) {
      this.getPartnerSteamBotItems = data.getPartnerSteamBotItems;
    }
  }

  get filteredGetPartnerSteamBotItems() {
    return this.getPartnerSteamBotItems.filter(
      ({ name }) =>
        name.toLowerCase().indexOf(this.filterGetPartnerSteamBotItems) !== -1
    );
  }

  get lastSelected() {
    return _.last(this.selected) || null;
  }
}
